import { LitElement, html, css } from "lit-element";


class GameC extends LitElement {
  static get properties() {
    return {
    };
  }

  constructor() {
    super();
  }

  static get styles() {
    return [css`/*Mobile first*/ */
      .my-video{
        width: 120px;
        height: 95px;
      }
      .video-cont{
        margin-left: -40px;
      }
      @media screen and (min-width: 600px) {
        .my-video{
          width: 620px;
          height: 315px;
        }
        .video-cont{
          margin-left: 0px;
        }
      }
    `];
  }

  render() {
    return html`
      <div>
        Diaphragmatic speaking</br>
        <ul>
          <li>The intention is to execute speak loudly enough + protecting the voice</li>
          <li>Todd's view</br>
            <div class="video-cont">
              <iframe class="my-video" width="280"
                src="https://www.youtube.com/embed/MuHauLVM5Z0?start=803&end=982">
              </iframe>
            </div></br>
          </li>
          <li>How to diaphragmatic breath?:</br>
            <div class="video-cont">
              <iframe class="my-video" width="280"
                src="https://www.youtube.com/embed/TZeyO79otSs">
              </iframe>
            </br>Watch out the warning at the end of the video*
            </div></br>
          </li>
          <li>Diaphragmatic speaking:</br>
            <div class="video-cont">
              <iframe class="my-video" width="280"
                src="https://www.youtube.com/embed/O7gCQwvQF9M">
              </iframe>
            </div></br>
          </li>
        </ul>
      </div>
    `;
  }
}
// Register the new element with the browser.
customElements.define("game-c", GameC);
