import { LitElement, html, css } from "lit-element";


import "@/components/lit/games/game-a.js";
import "@/components/lit/games/game-b.js";
import "@/components/lit/games/game-c.js";
import "@/components/lit/games/game-d.js";
class GameSelected extends LitElement {
  static get properties() {
    return {
      selection: { type: String }
    };
  }

  constructor() {
    super();
    this.selection = "choose";
    
    
  }

  static get styles() {
    return [css`
    .vertical-spaced{
      margin-top: 5px;
      margin-bottom: 5px;
    }`];
  }

  render() {
    const options= () =>
      html`
        <option value="choose">Back to menu</option>
        <option value="a">Non physical features</option>
        <option value="b">Reasons you're the best choice</option>
        <option value="c">Diaphragmatic speaking</option>
        <option value="d">Select D</option>
      `; 
    const selectGame = () =>
      html`
        <div>Choose a game <select @change="${() => this.changeSelection()}">${options()}</select></div>
      `;
      
      const chooseGame = () =>
      html`
        <div class="vertical-spaced">Here you have a list of all the available games, more are being developed</div>
        <div class="vertical-spaced">We really suggest you trying all of them since each targets a different part of game</div>
        <ul>
          <li>Non physical features
            <ul>
              <li>You type in 5 non physical features you want in a woman</li>
              <li>The game remembers your options and the last time you typed them in the browser you're using right now</li>
            </ul>
          </li>
          </br>
          <li>Reasons why you are the best choice
            <ul>
              <li>You type in 5 reasons why you are the best choice for her, as a lover or boyfriend, what you prefer </li>
              <li>The game remembers your options and the last time you typed them in the browser you're using right now</li>
            </ul>
          </li>
          </br>
          <li>Diaphragmatic speaking</li>
            <ul>
              <li>Todd's view is linked but I don't find it easy to perform</li>
              <li>Another point on diaphragmatic speaking is added here</li>
            </ul>
          </li>
          </br>
          <li>Game D</li>
        </ul>
      `;

      const chooseA = () =>
        html`
          <game-a></game-a>
        `;

      const chooseB = () =>
        html`
          <game-b></game-b>
        `;

      const chooseC = () =>
        html`
          <game-c></game-c>
        `;

      const chooseD = () =>
        html`
          <game-d></game-d>
        `;

    return html`
      <div class="game-selected">
        ${selectGame()}
        ${this.compGame() ? chooseGame() : ""}
        ${this.compA() ? chooseA() : ""}
        ${this.compB() ? chooseB() : ""}
        ${this.compC() ? chooseC() : ""}
        ${this.compD() ? chooseD() : ""}
      </div>
    `;
  }
  changeSelection() {
    this.selection = this.shadowRoot.querySelector('select').value;
  }

  compGame() {
    return this.selection == "choose";
  }
  compA() {
    return this.selection == "a";
  }
  compB() {
    return this.selection == "b";
  }
  compC() {
    return this.selection == "c";
  }
  compD() {
    return this.selection == "d";
  }
}
// Register the new element with the browser.
customElements.define("game-selected", GameSelected);
