import { LitElement, html, css } from "lit-element";

import "@/components/lit/games/game-selected.js";

class TheGame extends LitElement {
  static get properties() {
    return {};
  }

  constructor() {
    super();
  }

  static get styles() {
    return [
      css`
        :host {
          width:100%;
          height:100%;
        }
        .hidden{
          display:none;
        }
        .game-container{
          width: calc(100% - 40px);
          height:calc(100% - 40px);
          margin: 0 auto;
          padding: 10px 10px 0px 10px;

          border: 1.5px solid white;
          -webkit-border-radius: 3px;
          -moz-border-radius: 3px;
          border-radius: 3px;

          color: #EBE0DE;
          
          opacity: 1;
      
          animation: appearlate3 4s;
          animation-timing-function: cubic-bezier(1, 0.01, 0.62, 0.74);
        }        
    
        @keyframes appearlate3 {
          0% {
            opacity: 0;
          }
          80% {
            opacity: 0;
          }
          100% {
            opacity: 1;
          }
        }
      `,
    ];
  }

  render() {
    return html`
      <div class="game-container ${this.hidden ? `hidden` : ``}">
        <game-selected></game-selected>
      </div>
    `;
  }
}
// Register the new element with the browser.
customElements.define("the-game", TheGame);
